* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

#search-form {
  width: 100%;
  background-color: #3f56b1;
  justify-content: center;
  padding-top: 10px;
  padding-bottom: 10px;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  box-shadow: 0 5px 5px rgba(0, 0, 0, .15);
}

input {
  width: 320px;
  border: 0;
  border-radius: 4px 0 0 4px;
  outline: none;
  padding: 4px;
  font-family: inherit;
  font-size: 16px;
}

.button {
  cursor: pointer;
  border: 0;
  border-radius: 0 4px 4px 0;
  padding: 8px;
  font-family: inherit;
  font-size: 16px;
}

.load-more {
  cursor: pointer;
  letter-spacing: .06em;
  text-transform: uppercase;
  color: #fff;
  box-shadow: none;
  background-color: #3f56b1;
  border: 0;
  border-radius: 4px;
  margin: 15px auto 30px;
  padding: 8px 22px;
  font-family: inherit;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.8;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), box-shadow .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
}

.load-more:hover, .load-more:focus {
  background-color: #2f459a;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 65px;
  display: flex;
}

.photo-link {
  cursor: pointer;
  color: inherit;
  margin: 8px;
  text-decoration: none;
}

.photo-card {
  width: 350px;
  text-align: center;
  background-color: #f9f9f8;
  border-radius: 0 0 4px 4px;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

.photo-card img {
  width: 100%;
  height: 218px;
  object-fit: cover;
  display: block;
}

.info {
  justify-content: space-evenly;
  padding: 8px;
  display: flex;
}

.info-item {
  flex-direction: column;
  margin: 0;
  display: flex;
}

